/**
 * 
 *
 * baseUrl: 
 *
 */

let baseUrl = ''
if (process.env.NODE_ENV == 'development') {
		baseUrl = 'https://web1.robintse.cn/'
} else if (process.env.NODE_ENV == 'production') {
		baseUrl = 'http://wxshare.robintse.cn/api/'
}

export {
		baseUrl
}



